a {
    text-decoration: none;
}

@font-face {
    font-family: "Playtype";
    src: url("../../assets/Play-Bold.ttf") format("truetype");
}



.containerCard {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-around;
    padding-top: 70px;
}

.containerLoading {
    background-repeat: no-repeat;
    background-attachment: fixed;
    background-position: center;
    background-size: cover;
    align-items: center;
    justify-content: center;
    height: 100vh;
}

.searchBar {
    background-color: transparent;
    width: 100%;
    margin-bottom: 10px;
    display: flex;
    justify-content: space-around;
    padding: 1rem;
    font-size: 20px;
}

.select {
    font-family: 'My Font Name';
    font-size: 1px;
    border-color: #736b5e;
    display: inline-block;
    cursor: pointer;
    height: 30px;
    outline: 0;
    border: 0;
    border-radius: 0;
    background: transparent;
    font-size: 1rem;
    text-align-last: center;
    position: relative;
    -webkit-animation: glow 1000s ease-in-out infinite alternate;
    -moz-animation: glow 1000s ease-in-out infinite alternate;
    animation: glow 1000s ease-in-out infinite alternate;
    color: white;
}

.select option {
    font-family: 'My Font Name';
    color: white;
    background: rgba(146, 5, 5, 0.55);
    box-shadow: 0 8px 32px 0 rgba(31, 38, 135, 0.37);
    backdrop-filter: blur(7px);
    -webkit-backdrop-filter: blur(7px);
    border-radius: 10px;
    border: 1px solid rgba(255, 255, 255, 0.18);
}

.searchGame {
    display: inline-block;
    cursor: text;
    height: 30px;
    width: 400px;
    outline: 0;
    border: 0;
    border-radius: 0;
    background: transparent;
    font-size: 1.1em;
    font-family: 'My Font Name ';
    text-align-last: center;
    border: 1px solid;
    position: relative;
    border-radius: 30px;
    border-color: solid rgb(226, 10, 46);
    caret-color: transparent;
    color: white;
}

.searchBtn {
    font-family: 'My Font Name';
    border-radius: 50px;
    margin-left: 10px;
    width: 100px;
    height: 40px;
    font-size: 20px;
    color: white;
    -webkit-animation: glow 1000s ease-in-out infinite alternate;
    -moz-animation: glow 1000s ease-in-out infinite alternate;
    animation: glow 1000s ease-in-out infinite alternate;
    cursor: pointer;
}

.searchBtn:hover {
    background-color: #c20c33;
}

@keyframes glow {
    from {
        text-shadow: 0 0 2px #c90606, 0 0 2px #e60013, 0 0 2px #e90e0e, 0 0 50px #dd0b0b, 0 0 30px #ee0c0c;
    }

    to {
        text-shadow: 0 0 5px #fff, 0 0 7px #e20a0a, 0 0 30px #d1091a, 0 0 40px #eb0d2b;

    }
}

@keyframes glow {
    from {
        text-shadow: 0 0 10px #e60000, 0 0 10px #e60000, 0 0 30px #e60000, 0 0 40px #e60000, 0 0 20px #e60000;
    }

    to {
        text-shadow: 0 0 2px #c40505, 0 0 3px #db0707, 0 0 20px #e20510, 0 0 30px #eb0404ab;
    }
}

.createGame {
    font-family: 'My Font Name';
    font-size: 20px;
    height: 40px;
    border: none;
    outline: none;
    color: rgb(255, 254, 247);
    cursor: pointer;
    position: relative;
    z-index: 0;
    border-radius: 10px;
    text-shadow: 2px 2px 10px black;
    -webkit-animation: glow 1s ease-in-out infinite alternate;
    -moz-animation: glow 1s ease-in-out infinite alternate;
    animation: glow 1s ease-in-out infinite alternate;
    background-color: transparent;
}

.createGame::before {
    position: absolute;
    content: "";
    width: calc(100% + 4px);
    height: calc(100% + 4px);
    left: -2px;
    top: -2px;
    background: linear-gradient(124deg,
            #e40808,
            #ee0606,
            #db0606,
            #e00a0a,
            #e70d0d,
            #f00f0f,
            #eb0a0a,
            #dd0a0af3,
            #e60f0f);
    background-size: 400%;
    z-index: -1;
    filter: blur(5px);
    animation: move 20s linear infinite;
    opacity: 0;
    transition: opacity 0.3s ease-in-out;
    border-radius: 10px;
}

.createGame:hover::before {
    opacity: 1;
}

.createGame::after {
    z-index: -1;
    content: "";
    position: absolute;
    width: 100%;
    height: 100%;
    background: transparent;
    left: 0;
    top: 0;
    border-radius: 10px;
}

@keyframes move {
    0% {
        background-position: 0 0;
    }

    50% {
        background-position: 400% 0;
    }

    100% {
        background-position: 0 0;
    }
}

.refresh {
    font-family: 'My Font Name';
    font-size: 1    0px;
    background-color: transparent;
    color: rgb(245, 244, 241);
    
    border-radius: 10px;
    background-color: transparent;
    -webkit-animation: glow 1000s ease-in-out infinite alternate;
    -moz-animation: glow 1000s ease-in-out infinite alternate;
    animation: glow 1000s ease-in-out infinite alternate;
    height: 40px;
    cursor: pointer;
}

.refresh:hover {
    color: white
}