
.container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100%;
    border-radius: 40px;
    background: rgba(3, 3, 25, 0.4);
    box-shadow: 0 8px 32px 0 rgba(31, 38, 135, 0.37);
    backdrop-filter: blur(8.5px);
    -webkit-backdrop-filter: blur(8.5px);
    border-radius: 20px;
    border: ridge #ffffff;
    margin-top: none;
    margin: 1%;
    color: white;
}

.inputForm {
    margin: 10px;
}

h1 {
    font-family: 'My Font Name';
    font-size: 50px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    -webkit-animation: glow 1000s ease-in-out infinite alternate;
    -moz-animation: glow 1000s ease-in-out infinite alternate;
    animation: glow 1000s ease-in-out infinite alternate;
}

.container h4 {
    font-family: 'My Font Name';
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    margin-top: 7px;
    text-shadow: 1px 1px 2px black;
    text-decoration: underline;
}



.container h5 {
    font-family: 'My Font Name';
    margin: 5px;
    font-size: 20px;
    -webkit-animation: glow 1000s ease-in-out infinite alternate;
    -moz-animation: glow 1000s ease-in-out infinite alternate;
    animation: glow 1000s ease-in-out infinite alternate;
}

.container input {
    width: 200px;
    height: 30px;
    margin: 7px;
    background-color: transparent;
    color: white;
}

.nameCss {
    margin: 10px
}

.raiting {
    margin: 10px
}

select {
    background-color: blue;
    width: 150px;
    height: 25px;
    color: white;
}

li {
    font-family: 'My Font Name';
    font-size: 15px;
    margin-top: 1px;
    margin-bottom: 1px;


}

.deleteBtn {
    margin: 5px;
    height: 20px;
    width: 20px;
    border-radius: 100px;
    border-color: #270e97;

}

.deleteBtn:hover {
    background-color: rgb(24, 9, 90);
    -webkit-animation: glow 1000s ease-in-out infinite alternate;
    -moz-animation: glow 1000s ease-in-out infinite alternate;
    animation: glow 1000s ease-in-out infinite alternate;

}



@keyframes glow {
    from {
        text-shadow: 0 0 20px #f10c0c, 0 0 30px #f80707, 0 0 40px #ff0808, 0 0 50px #f80101, 0 0 30px #ee0c0c;
    }

    to {
        text-shadow: 0 0 5px #fff, 0 0 7px #ff0000, 0 0 30px #f30101, 0 0 60px #ff0000;

    }
}

.createGameBtn {
    background-color: transparent;
    font-family: 'My Font Name';
    font-size: 15px;
    width: 250px;
    height: 30px;
    -webkit-animation: glow 1000s ease-in-out infinite alternate;
    -moz-animation: glow 1000s ease-in-out infinite alternate;
    animation: glow 1000s ease-in-out infinite alternate;
    border-radius: 2rem;
    margin: 10px;
    color: white; 
}

.createGameBtn:hover {
    opacity: 1;
    background-color: #ff0000;
    cursor: pointer;
}
